/* Layout Fix */
html, body, #root {
  height: 100%;
  margin: 0;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  padding: 40px 0;
}

/* Navbar & Header */
.header {
  background-color: #0047AB;
  color: white;
  padding: 15px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-links a {
  color: white;
  margin: 0 15px;
  text-decoration: none;
  font-size: 16px;
}

.nav-links a:hover {
  text-decoration: underline;
}

/* Page container */
.page-wrapper {
  text-align: center;
  padding-bottom: 20px;
}

/* Project & Service Card Styling */
.project-page, .services-page {
  text-align: center;
  margin: 0 auto;
  padding: 20px;
  max-width: 900px;
}

.project-details, .services-list, .education-details {
  text-align: left;
  display: inline-block;
}

/* Contact Page */
.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-container input,
.contact-container textarea {
  width: 450px;
  padding: 10px;
  margin: 5px 0;
}

button {
  background-color: #0047AB;
  color: white;
  border: none;
  padding: 8px 15px;
  cursor: pointer;
  margin-top: 8px;
}

button:hover {
  background-color: #0066CC;
}

/* Footer */
.footer {
  text-align: center;
  padding: 15px 0;
  background-color: #F1F1F1;
  font-size: 14px;
}
