.header {
  background-color: #0d47a1;
  padding: 20px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

/* Logo Styling */
.logo {
  font-size: 28px;
  font-weight: bold;
  color: white;
}

/* Navigation Links */
nav a {
  color: white;
  margin: 0 15px;
  text-decoration: none;
  font-size: 16px;
}

nav a:hover {
  text-decoration: underline;
}

/* Responsive Navigation */
@media (max-width: 768px) {
  .header {
    flex-direction: column;
    text-align: center;
  }

  nav a {
    display: block;
    margin: 10px 0;
  }
}
